<template>
    <div>
        <img class="img-fluid pt-5 mt-5 w-25 mx-auto d-block" alt="logo tukarjual.com" src="../../assets/site-logo.webp">
        <h6 class="text-center mb-5">Pian ingati password nya lah !</h6>
        <form @submit.prevent="reset_password">
            <div class="form-group">
                <div class="col">
                    <input required v-model="number_phone" type="tel" name="number_phone" placeholder="Masukkan nomor whatsapp" class="form-control form-control-lg bg-light">
                </div>
            </div>
            <div class="form-group">
                <div class="col">
                    <div class="d-flex justify-content-center">
                        <input required :disabled="!disabledOTP" v-model="code" type="text" name="" placeholder="Masukkan OTP" class="form-control form-control-lg bg-light mr-2">
                        <button @click="send_code" v-if="!sended_code" :disabled="!valid_number_phone" class="btn col-5 float-right text-white tombol">Dapatkan kode</button>
                        <button v-else class="btn btn-outline-info col-5 float-right">{{timeout+' detik'}}</button>
                    </div>
                </div>
            </div>
            <div class="form-group">
                <div class="col">
                    <div class="input-group mb-2">
                        <input required name="new_password" autocomplete="new-password" v-model="password" :type="hidden_password ? 'password' : 'text'" placeholder="Masukkan kata sandi baru" class="form-control form-control-lg bg-light border-right-0">
                        <div class="input-group-append">
                            <div class="input-group-text bg-light">
                                <i v-if="hidden_password" @click="hidden_password=false" class="fa fa-eye-slash"></i>
                                <i v-else @click="hidden_password=true" class="fa fa-eye"></i>
                            </div>
                        </div>
                    </div>
                    <small class="text-muted">Kata sandi harus berisi minimal 6 karakter</small>
                </div>
            </div>
            <div class="col pt-3">
                <input type="submit" value="Reset Password" class="btn btn-lg btn-block text-white tombol">
            </div>
        </form>
        <modal-warning :ids="'number_phone_not_exists'" :message="'Nomor HP tidak terdaftar'"></modal-warning>
        <modal-warning :ids="'wrong_otp'" :message="'OTP yang anda masukkan salah'"></modal-warning>
    </div>
</template>
<script>
import modalWarning from '@/components/modal/Failed'
import $ from 'jquery'
export default {
    name: "Register",
    data(){
        return {
            number_phone:"",
            last_number_phone:"",
            password:"",
            code:"",
            hidden_password:true,
            sended_code:false,
            disabledOTP:false,
            timeout:60,
            interval:null,
        }
    },
    components:{
        modalWarning
    },
    computed:{
        valid_number_phone(){
            return this.number_phone.length>=10;
        },
        valid_password(){
            return this.password.length>=6;
        },
        valid_code(){
            return this.code.length==5;
        }
    },
    watch:{
        timeout(value){
            if(value==0){
                this.sended_code = false;
                clearInterval(this.interval)
                this.interval = null;
                this.timeout = 60;
            }
        }
    },
    methods:{
        send_code(){
            this.last_number_phone = this.number_phone;
            this.axios.get(this.$store.state.url_api + 'auth/check_phone_to_reset_password/'+this.last_number_phone).then(response => {
                if(response.data.phone_ok){
                    this.axios.post(this.$store.state.url_api + 'auth/send-code', {
                        number_phone : this.number_phone
                    }).then(response => {
                        if(response.data.meta.success){
                            this.disabledOTP = true;
                            this.sended_code = true;
                            var that = this;
                            this.interval = setInterval(function(){
                                that.timeout--;
                            },1000);
                        }
                    })
                }else{
                    $("#number_phone_not_exists").modal('show')
                }
            })
        },

        reset_password(){
            this.axios.post(this.$store.state.url_api + 'auth/reset_password', {
                number_phone: this.last_number_phone,
                password: this.password,
                code:this.code
            }).then(response => {
                if(response.data.meta.success){
                    this.$router.push({name:'login'});
                }else{
                    $('#wrong_otp').modal('show')
                }
            })
        }


    }
}
</script>
<style scoped>
    input:disabled{
        background: rgb(199, 199, 199) !important;
        opacity: .6 !important;
    }
</style>